import React, { Component } from 'react';
import './StateDetails.css';
import $ from 'jquery';
import Card from '@material-ui/core/Card';
import _ from 'lodash';


class StateDetails extends Component {
  constructor(props) {
    super(props);
    this.state = {
      dynamicClassname:'extraCls',
      sliderLog:'',
      clearhandleautoslide:'',
      StateWiseData:[]
    }
  }
  componentDidMount(){
    this.autoSlideCards();
    this.fetchStatesDetails();
  }
  fetchStatesDetails(){
    fetch('https://api.covid19india.org/state_district_wise.json')
    .then(res => res.json())
    .then(statewisedetails =>{ 

      console.log('StateWiseDate',statewisedetails);
      this.setState({ StateWiseData : {...statewisedetails} })
      let convertArry = [];
      convertArry.push(this.state.StateWiseData);
      let maharashtra =  _.groupBy(this.state.StateWiseData,'Maharashtra')
      console.log('StateWiseDate set to state',maharashtra);
    })
  }
  autoSlideCards(){
    clearInterval(this.state.clearhandleautoslide);
    this.state.sliderLog = setInterval(()=>{
      let iscardDisabled = document.getElementsByClassName('flickity-button flickity-prev-next-button next')[0].disabled; 
      if(!iscardDisabled){
        let slideClick = document.getElementsByClassName('flickity-button flickity-prev-next-button next')[0];
        slideClick.click();
      }else{
        let gotostart = document.getElementsByClassName('flickity-page-dots')[0].children[0];
        gotostart.click()
      }
    },5000)
  }
  handleflipCard(extra) {
    clearInterval(this.state.sliderLog)

    this.state.clearhandleautoslide = setInterval(()=>{
      this.autoSlideCards()
    },20000)

    let cardTransitionTime = 500;

    let $card = $('.'+extra)
    let switching = false

    if (switching) {
      return false
    }
    switching = true

    $card.toggleClass('is-switched')
    window.setTimeout(function () {
      $card.children().children().toggleClass('is-active')
      switching = false
    }, cardTransitionTime / 2)
  }
  
  render() {
    const cardStlying = {
      boxShadow: '0 10px 20px rgba(10,10,10,0.40)'
      }
    return (
      <React.Fragment>
        <div className="gallery js-flickity"
          data-flickity-options='{ "freeScroll": true }'>
          <div title="Click For More Detalis" className={"gallery-cell js-card "+ this.state.dynamicClassname} onClick={this.handleflipCard.bind(this,this.state.dynamicClassname)}>
          <Card variant="outlined" style={cardStlying}>
            <div className="card__wrapper">
              <div className="card__side is-active">
                <h1>FRONT</h1>
                <h1>FRONT</h1>
                <h1>FRONT</h1>
              </div>
              <div className="card__side card__side--back">
                <h2>BACK</h2>
                <h2>BACK</h2>
                <h2>BACK</h2>
                <h2>BACK</h2>
                <h2>BACK</h2>
                <h2>BACK</h2>
              </div>
            </div>
            </Card>
          </div>
          <div className="gallery-cell js-card" onClick={this.handleflipCard.bind(this)}>
          <Card variant="outlined" style={cardStlying}>
          <div className="card__wrapper">
              <div className="card__side is-active">
                <h1>FRONT</h1>
                <h1>FRONT</h1>
                <h1>FRONT</h1>
              </div>
              <div className="card__side card__side--back">
                <h2>BACK</h2>
                <h2>BACK</h2>
                <h2>BACK</h2>
                <h2>BACK</h2>
                <h2>BACK</h2>
                <h2>BACK</h2>
              </div>
            </div>
            </Card>
          </div>
          <div className="gallery-cell js-card" onClick={this.handleflipCard.bind(this)}>
          <Card variant="outlined" style={cardStlying}>
          <div className="card__wrapper">
              <div className="card__side is-active">
                <h1>FRONT</h1>
                <h1>FRONT</h1>
                <h1>FRONT</h1>
              </div>
              <div className="card__side card__side--back">
                <h2>BACK</h2>
                <h2>BACK</h2>
                <h2>BACK</h2>
                <h2>BACK</h2>
                <h2>BACK</h2>
                <h2>BACK</h2>
              </div>
            </div>
            </Card>
          </div>
          <div className="gallery-cell js-card" onClick={this.handleflipCard.bind(this)}>
          <Card variant="outlined" style={cardStlying}>
          <div className="card__wrapper">
              <div className="card__side is-active">
                <h1>FRONT</h1>
                <h1>FRONT</h1>
                <h1>FRONT</h1>
              </div>
              <div className="card__side card__side--back">
                <h2>BACK</h2>
                <h2>BACK</h2>
                <h2>BACK</h2>
                <h2>BACK</h2>
                <h2>BACK</h2>
                <h2>BACK</h2>
              </div>
            </div>
            </Card>
          </div>
          <div className="gallery-cell js-card" onClick={this.handleflipCard.bind(this)}>
          <Card variant="outlined" style={cardStlying}>
          <div className="card__wrapper">
              <div className="card__side is-active">
                <h1>FRONT</h1>
                <h1>FRONT</h1>
                <h1>FRONT</h1>
              </div>
              <div className="card__side card__side--back">
                <h2>BACK</h2>
                <h2>BACK</h2>
                <h2>BACK</h2>
                <h2>BACK</h2>
                <h2>BACK</h2>
                <h2>BACK</h2>
              </div>
            </div>
            </Card>
          </div>
        </div>
      </React.Fragment>
    );
  }
}

export default StateDetails;